.btn {
	--h: 2.5rem;
	--fz: 1.0625rem;
	--fw: 600;
	--c: var(--pr);
	--c-h: var(--pr);
	--t-style: uppercase;
	--flex-jc: flex-start;
	--flex-ai: center;
	color: var(--c);
	font-size: var(--fz);
	min-height: var(--h);
	font-weight: var(--fw);
	line-height: 1;
	text-transform: var(--t-style);
	display: inline-flex;
	justify-content: var(--flex-jc);
	align-items: var(--flex-ai);
	letter-spacing: 0.053125rem;
	&:hover {
		color: var(--c-h); }
	&-icon {
		width: 2.5rem;
		height: 2.5rem;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-left: 0.625rem;
		.icon {
			--sz: 0.875rem;
			width: var(--sz);
			height: var(--sz);
			position: relative;
			top: 0.0625rem; }
		&--fill {
			background-color: var(--pr);
			color: var(--wh);
			border-radius: 50%;
			.icon {
				--sz: 12px;
				top: 0; } } }
	&-transparent {
		background-color: transparent;
		border: none;
		outline: none; }
	&-circle {
		--fw: 400;
		--sz: 4.5rem;
		--bg-color: var(--bl);
		--bg-color-h: var(--bl);
		--c: var(--wh);
		--flex-jc: center;
		--flex-ai: center;
		width: var(--sz);
		height: var(--sz);
		background-color: var(--bg-color);
		color: var(--c);
		border-radius: 50%;
		&:hover {
			background-color: var(--bg-color-h);
			color: var(--wh); }
		&-sm {
			--sz: 2.5rem;
			.icon {
				--sz: 1.25rem; } }
		&-primary {
			--bg-color: var(--pr);
			--bg-color-h: var(--pr);
			--c: var(--wh); } } }
